<template>
    <div class="role">
        <el-row :gutter="20" class="functionalArea">
            <el-col :span="6">
                <el-button-group>
                    <el-button type="primary" icon="el-icon-plus" @click="addBtnFn"></el-button>
                    <el-button type="primary" icon="el-icon-share"></el-button>
                    <el-button type="primary" icon="el-icon-delete" @click="clearBtnFn"></el-button>
                </el-button-group>
            </el-col>
            <el-col :span="6" :offset="12">
                <el-input placeholder="请输入内容" v-model="searchInput" clearable>
                    <template slot="append">
                        <el-button type="success" plain>搜索</el-button>
                    </template>
                </el-input>
            </el-col>
        </el-row>

        <el-table
                ref="multipleTable"
                :data="tableData"
                style="width: 100%"
                :header-cell-style="headerCellStyle"
                :cell-style="cellStyle"
                @select="handleSelectionChange"
                @select-all="selectAllFn"
        >
            <el-table-column
                    type="selection"
                    width="55">
            </el-table-column>
            <el-table-column
                    label="序号"
                    width="60">
                <template slot-scope="scope">
                    <span style="margin-left: 10px">{{ scope.row.index }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="角色"
                    width="240">
                <template slot-scope="scope">
                    <span style="margin-left: 10px">{{ scope.row.name }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="操作"
                    min-width="240">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-dialog title="添加角色" :visible.sync="addFormVisible" width="800px">
            <el-form :model="form">
                <el-row :gutter="20">
                    <el-col :span="24">
                        <el-form-item label="角色" :label-width="formLabelWidth">
                            <el-input v-model="form.name" placeholder="请输入角色" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="权限树" :label-width="formLabelWidth">
                            <el-tree
                                    :data="jurisdictionData"
                                    node-key="id"
                                    show-checkbox
                                    ref="tree"
                                    :default-expand-all="false"
                                    :expand-on-click-node="false"
                            >
                            </el-tree>
                        </el-form-item>
                    </el-col>
                    <!--<el-col :span="12">-->
                        <!--<el-form-item label="流程" :label-width="formLabelWidth">-->
                            <!--<el-input v-model="form.process" placeholder="格式：1-2-3" autocomplete="off"></el-input>-->
                        <!--</el-form-item>-->
                    <!--</el-col>-->
                    <!--<el-col :span="12">-->
                        <!--<el-form-item label="时限" :label-width="formLabelWidth">-->
                            <!--<el-input v-model="form.timeout" placeholder="请输入时限(天)" autocomplete="off"></el-input>-->
                        <!--</el-form-item>-->
                    <!--</el-col>-->
                    <!--<el-col :span="12">-->
                        <!--<el-form-item label="类型" :label-width="formLabelWidth">-->
                            <!--<el-select v-model="form.cate" placeholder="请选择类型" style="width:100%;">-->
                                <!--<el-option v-for="(item, index) in styleList" :key="index" :label="item.label" :value="item.val"></el-option>-->
                            <!--</el-select>-->
                        <!--</el-form-item>-->
                    <!--</el-col>-->
                    <!--<el-col :span="12">-->
                        <!--<el-form-item label="常办" :label-width="formLabelWidth">-->
                            <!--<el-select v-model="form.frequent" placeholder="请选择服务区" style="width:100%;">-->
                                <!--<el-option v-for="(item, index) in frequentList" :key="index" :label="item.label" :value="item.val"></el-option>-->
                            <!--</el-select>-->
                        <!--</el-form-item>-->
                    <!--</el-col>-->
                </el-row>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="addSureFn">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="编辑角色" :visible.sync="fixFormVisible" width="800px">
            <el-form :model="form1">
                <el-row :gutter="20">
                    <el-col :span="24">
                        <el-form-item label="角色" :label-width="formLabelWidth">
                            <el-input v-model="form1.name" placeholder="请输入角色" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="权限树" :label-width="formLabelWidth">
                            <el-tree
                                    :data="jurisdictionData"
                                    node-key="id"
                                    show-checkbox
                                    ref="tree1"
                                    :default-expand-all="false"
                                    :expand-on-click-node="false"
                            >
                            </el-tree>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="fixFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="fixSureFn">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
let startJurisdiction = {
  'data': [
    {
      'children': [],
      'id': 'root',
      'is_function': 'N',
      'label': 'root',
      'pid': null,
      'routing': ''
    }
  ],
  'errmsg': '成功',
  'errno': '0'
}
export default {
  name: 'ATheme',
  data () {
    return {
      searchInput: '',
      tableData: [],
      jurisdictionData: [],

      addFormVisible: false,
      formLabelWidth: '120px',
      form: {
        name: '',
        permissions: []
      },
      fixFormVisible: false,
      form1: {
        name: '',
        roleId: '',
        permissions: []
      },

      selectIds: []
    }
  },
  watch: {
  },
  created: function () {
    this.getRoleFn()
    this.getJurisdictionFn()
  },
  methods: {
    addBtnFn () {
      this.addFormVisible = true
    },
    addSureFn () {
      let add = this.$refs.tree.getCheckedNodes()
      let addPermissionsId = []
      add.forEach(function (val, i) {
        if (val.id !== 'root') {
          addPermissionsId.push(val.id)
        }
      })
      let temp = {
        name: this.form.name.replace(/(^\s*)|(\s*$)/g, ''),
        permission_ids: addPermissionsId,
        user_id: sessionStorage.user_id
      }
      if (temp.name !== '') {
        this.addFn(temp)
      } else {
        this.notifyFn('提示', '请补全信息')
      }
    },
    handleEdit (index, row) {
      console.log(index, row)
      this.fixFormVisible = true
      this.form1.name = row.name
      this.form1.roleId = row.id

      let self = this
      setTimeout(function () {
        self.$refs.tree1.setCheckedNodes(row.permissions)
      }, 200)
    },
    fixSureFn () {
      let add = this.$refs.tree1.getCheckedNodes()
      let addPermissionsId = []
      add.forEach(function (val, i) {
        if (val.id !== 'root') {
          addPermissionsId.push(val.id)
        }
      })
      let temp = {
        id: this.form1.roleId,
        name: this.form1.name.replace(/(^\s*)|(\s*$)/g, ''),
        permission_ids: addPermissionsId,
        user_id: sessionStorage.user_id
      }
      if (temp.name !== '') {
        this.fixFn(temp)
      } else {
        this.notifyFn('提示', '请补全信息')
      }
    },
    clearBtnFn () {
      if (this.selectIds.length) {
        this.$confirm('确定删除所选角色', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let params = {
            id_list: this.selectIds,
            user_id: sessionStorage.user_id
          }
          this.deleteSingleFn(params)
        }).catch(() => {
          this.notifyFn('提示', '已取消删除')
        })
      } else {
        this.notifyFn('提示', '未选择角色')
      }
    },
    handleDelete (index, row) {
      console.log(row)
      this.$confirm('确定删除角色：' + row.name, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let params = {
          id_list: [row.id],
          user_id: sessionStorage.user_id
        }
        this.deleteSingleFn(params)
      }).catch(() => {
        this.notifyFn('提示', '已取消删除')
      })
    },
    async getRoleFn () {
      let params = {}
      try {
        let res = await this.$api.unsplash.getRoles(params)
        if (res.errno === '0') {
          this.tableData = this.addSequence(res.data)
          console.log(res.data)
        } else {
          this.notifyFn('提示', '获取角色失败')
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async getJurisdictionFn () {
      let params = {}
      try {
        let res = await this.$api.unsplash.getJurisdictions(params)
        if (res.errno === '0') {
          startJurisdiction.data[0].children = res.data.slice()
          this.jurisdictionData = startJurisdiction.data
        } else {
          this.notifyFn('提示', '获取权限失败')
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async addFn (params) {
      try {
        let res = await this.$api.unsplash.postAddRole(params)
        console.log(res)
        if (res.errno === '0') {
          this.addFormVisible = false
          this.notifyFn('提示', '增加角色成功', 'success')
          this.getRoleFn()
        } else {
          this.notifyFn('提示', '增加角色失败')
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async fixFn (params) {
      try {
        let res = await this.$api.unsplash.postfixRole(params)
        if (res.errno === '0') {
          this.fixFormVisible = false
          this.notifyFn('提示', '修改角色成功', 'success')
          this.getRoleFn()
        } else {
          this.notifyFn('提示', '修改角色失败')
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async deleteSingleFn (params) {
      try {
        let res = await this.$api.unsplash.postClearRole(params)
        if (res.errno === '0') {
          this.notifyFn('提示', '删除成功', 'success')
          this.selectIds = []
          this.getRoleFn()
        } else {
          this.notifyFn('提示', '删除失败')
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    handleSelectionChange (selection, row) { // 单击
      console.log(selection)
      this.selectIds = []
      for (let i = 0; i < selection.length; i++) {
        this.selectIds.push(selection[i].id)
      }
    },
    selectAllFn (selection) { // 全选
      console.log(selection)
      this.selectIds = []
      for (let i = 0; i < selection.length; i++) {
        this.selectIds.push(selection[i].id)
      }
    },
    headerCellStyle ({ row, column, rowIndex, columnIndex }) {
      if (rowIndex > -1) {
        return {
          'text-align': 'center'
        }
      }
    },
    cellStyle ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex >= 0) {
        return {
          'text-align': 'center'
        }
      }
    },
    myAlert (txt, title, self) {
      self.$alert(txt, title, {
        confirmButtonText: '确定',
        callback: action => {
        }
      })
    },
    addSequence (temp) {
      for (let i = 0; i < temp.length; i++) {
        temp[i].index = i + 1
      }
      return temp
    },
    notifyFn (title, msg, type = 'warning') {
      this.$notify({
        title: title,
        message: msg,
        type: type,
        dangerouslyUseHTMLString: true,
        duration: 5000,
        position: 'top-right'
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
    .role{
        .functionalArea{
            height: 80px;
        }
        .headerCellStyle{
            text-align: center;
        }
        .el-dialog__body {
            padding: 30px 60px 30px 20px;
        }
    }
</style>



